import { useEffect, useMemo, useState } from 'react';
import sounds from '../assets/sounds/index'
import { useSelector } from 'react-redux';
import useSound from 'use-sound';
import { adjustVolume } from '../helpers/volume';

function Bgm() {
  const bgmVolume = adjustVolume(useSelector((state) => state.game.bgmVolume));
  const opts = {
    loop: true,
    volume: bgmVolume,
    interrupt: true,
    onfade() {
      if(this.volume() < 0.001) {
        this.stop();
      }
    }
  };
  
  const menuBgm = useSound(sounds.menuBgmSrc, opts);
  const playBgm = useSound(sounds.playBgmSrc, opts);
  const resultsBgm = useSound(sounds.resultsBgmSrc, opts);
  const soundMap = useMemo(() => ({
    menuBgm,
    playBgm,
    resultsBgm,
  }), [menuBgm, playBgm, resultsBgm]);

  const currentScreen = useSelector((state) => state.game.currentScreen);
  const scheduledScreen = useSelector((state) => state.game.scheduledScreen);
  const roundPhase = useSelector((state) => state.round.roundPhase);
  const activeScreen = scheduledScreen ?? currentScreen;

  let currentSound = 'menuBgm';

  if(activeScreen === 'preloader') {
    currentSound = null;
  }else if(activeScreen === 'play') {
    if(roundPhase === 'results') currentSound = 'resultsBgm';
    else currentSound = 'playBgm';
  }

  const [currentlyScheduledSound, setCurrentlyScheduledSound] = useState(null);
  const [currentlyPlayingSound, setCurrentlyPlayingSound] = useState(null);
  
  useEffect(() => {
    if(currentlyScheduledSound === currentSound) return;
    if(currentlyScheduledSound) {
      // eslint-disable-next-line no-unused-vars
      const [ _, { sound } ] = soundMap[currentlyScheduledSound];
      sound.fade(bgmVolume, 0, 500);
    }
    setCurrentlyScheduledSound(currentSound);
  }, [currentSound, currentlyScheduledSound, soundMap, bgmVolume])

  useEffect(() => {
    if(!currentlyScheduledSound || currentlyPlayingSound === currentlyScheduledSound) return;
    const [ play, { sound } ] = soundMap[currentlyScheduledSound];
    const playTimeoutId = setTimeout(() => {
      setCurrentlyPlayingSound(currentlyScheduledSound);
      play();
      sound.fade(0, bgmVolume, 200);
    }, 400);
    return () => clearTimeout(playTimeoutId);
  }, [currentlyPlayingSound, currentlyScheduledSound, soundMap, bgmVolume])

  return null;
}

export default Bgm;
