
export const SET_CURRENT_SCREEN = 'SET_CURRENT_SCREEN';
export const SCHEDULE_SCREEN_CHANGE = 'SCHEDULE_SCREEN_CHANGE';
export const SET_SETTING = 'SET_SETTING';

export const setCurrentScreen = (screen) => ({
  type: SET_CURRENT_SCREEN,
  payload: screen
});

export const scheduleScreenChange = (screen) => ({
  type: SCHEDULE_SCREEN_CHANGE,
  payload: screen
});

export const setSetting = (setting, value) => ({
  type: SET_SETTING,
  payload: {setting, value}
});