import dictionaryEn from '../assets/dic'
import nspell from 'nspell';

import { SET_ROUND_PHASE, SETUP_ROUND, ADD_LOCAL_PLAYER_INPUT, UPDATE_PHASE_SECONDS_LEFT } from './actions/round'
import { logError } from '../helpers/log'

const spell = nspell(dictionaryEn);

const prepPhaseDuration = 4;

const initialState = {
  roundPhase: null, // prep, ongoing, results
  started: false,
  phaseSecondsLeft: null,
  nextPhaseDate: null,
  localPlayerInput: [ { content: '' } ],
  ongoingPhaseDuration: 10,
  bans: [],
  roundStartDate: null,
  evalError: false,
  results: {
    dupes: 0,
    banned: 0,
    mistakes: 0,
    spaces: 0,
    words: 0,
    letters: 0,
  }
};

function evaluatePlayerInput({ playerInput, bans }) {
  if(!spell) {
    logError("roundReducer: evaluatePlayerInput: spell check dictionary not loaded! Cannot evaluate.");
    return null;
  }
  const results = { ...initialState.results };
  const newPlayerInput = [];
  const banSet = new Set(bans);
  const encountered = new Set();
  for(let inputWord of playerInput) {
    if(!inputWord.content) continue;
    results.spaces++;
    if([...inputWord.content].some(char => banSet.has(char))) {
      results.banned++;
      newPlayerInput.push({
        ...inputWord,
        banned: true,
      });
      continue;
    }
    if(!spell.correct(inputWord.content)) {
      results.mistakes++;
      newPlayerInput.push({
        ...inputWord,
        mistake: true,
      });
      continue;
    }
    if(encountered.has(inputWord.content)) {
      results.dupes++;
      newPlayerInput.push({
        ...inputWord,
        dupe: true,
      });
      continue;
    }
    encountered.add(inputWord.content);
    newPlayerInput.push({ 
      ...inputWord,
      valid: true
    });
    results.letters += inputWord.content.length;
    results.words++;
  }
  if(results.spaces)
    results.spaces--; // remove trailing space
  
  return {
    results,
    newPlayerInput
  };
}

function updatePhaseSecondsLeft(state) {
  const delta = (state.nextPhaseDate - new Date())/1000;
  const wholeDelta = Math.ceil(delta);
  const targetPhaseSecondsLeft = delta > -1 ? wholeDelta : null;
  if(targetPhaseSecondsLeft !== state.phaseSecondsLeft) {
    return { ...state, phaseSecondsLeft: targetPhaseSecondsLeft };
  }
  return state;
}

const roundReducer = (state = initialState, action) => {
  switch (action.type) {
    case SETUP_ROUND:
      return {
        ...state,
        ongoingPhaseDuration: action.payload.ongoingPhaseDuration,
        bans: action.payload.bans,
      };
    case UPDATE_PHASE_SECONDS_LEFT: {
      return updatePhaseSecondsLeft(state);
    }
    case SET_ROUND_PHASE: {
      switch (action.payload) {
        case 'prep':
          return updatePhaseSecondsLeft({ 
            ...state, 
            roundPhase: action.payload, 
            results: { ...initialState.results },
            localPlayerInput: [ ...initialState.localPlayerInput ],
            started: true,
            nextPhaseDate: +new Date() + 1000 * prepPhaseDuration
          });
        case 'ongoing':
          return updatePhaseSecondsLeft({ 
            ...state,
            roundPhase: action.payload,
            roundStartDate: +new Date(),
            nextPhaseDate: +new Date() + 1000 * state.ongoingPhaseDuration
          });
        case 'results': {
          const localPlayerEval = evaluatePlayerInput({
            playerInput: state.localPlayerInput,
            bans: state.bans,
          });
          if(!localPlayerEval) {
            return updatePhaseSecondsLeft({
              ...state, 
              roundPhase: action.payload,
              nextPhaseDate: null,
              evalError: true,
            });
          }
          return updatePhaseSecondsLeft({
            ...state, 
            roundPhase: action.payload,
            nextPhaseDate: null,
            evalError: false,
            localPlayerInput: localPlayerEval.newPlayerInput,
            results: localPlayerEval.results,
          });
        }
        default:
          logError("roundReducer: SET_ROUND_PHASE: Unknown round phase:", action);
          return state;
      }
    }
    case ADD_LOCAL_PLAYER_INPUT: {
      if(state.roundPhase !== 'ongoing') return state;
      if(action.payload  === ' ') {
        if(state.localPlayerInput.length === 0 || state.localPlayerInput[state.localPlayerInput.length-1].content.length === 0) {
          return state; // ignore duplicate spaces
        }
        return {
          ...state,
          localPlayerInput: [
            ...state.localPlayerInput.slice(0, state.localPlayerInput.length-1),
            {
              ...state.localPlayerInput[state.localPlayerInput.length-1],
              finishedAt: +new Date(),
            },
            {
              content: '',
            }
          ],
        };
      }
      return { 
        ...state, 
        localPlayerInput: [
          ...state.localPlayerInput.slice(0, state.localPlayerInput.length-1),
          {
            content: state.localPlayerInput[state.localPlayerInput.length-1].content + action.payload
          }
        ],
      };
    }
    default:
      return state;
  }
};

export default roundReducer;