
import useSound from 'use-sound';
import './TitledLayout.css'
import { useCallback } from 'react';
import sounds from '../assets/sounds';

import './Buttons.css'
import { useSelector } from 'react-redux';
import { adjustVolume } from '../helpers/volume';

export default function LargeButton({ disableHoverSound = false, children, onClick }) {
  const uiVolume = adjustVolume(useSelector((state) => state.game.uiVolume));

  const opts = {
    interrupt: true,
    volume: uiVolume,
  };

  const [ hoverSound ] = useSound(sounds.buttonHoverBlipSrc, { 
    ...opts, 
    soundEnabled: !disableHoverSound
  });
  const [ clickSound ] = useSound(sounds.buttonBlipSrc, opts);

  const clickHandler = useCallback(() => {
    if(onClick) {
      clickSound();
      onClick();
      document.activeElement.blur();
    }
  }, [clickSound, onClick]);

  return <button className="large-button"
    onClick={clickHandler}
    onMouseOver={disableHoverSound ? undefined : hoverSound}
  >{children}</button>;
}
