import { useCallback, useEffect, useState } from "react"
import { useDispatch } from 'react-redux';
import { scheduleScreenChange } from "../../reducers/actions/game";
import TitledLayout from "../TitledLayout";

import LargeButton from "../LargeButton";

export default function Preloader() {
  const dispatch = useDispatch();
  
  const gotoMenu = useCallback(() => {
    dispatch(scheduleScreenChange('menu'));
  }, [dispatch]);

  const [loaded, setLoaded] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      // todo: load key assets
      setLoaded(true)
    }, 1000);
  }, [])

  return <TitledLayout className="menu-screen" title={loaded ? <h1>Game Loaded</h1> : <h1>Loading Game Assets...</h1>}>
    <div className="main-menu-buttons">
      { loaded 
        ? <LargeButton disableHoverSound onClick={gotoMenu}>Start</LargeButton> 
        : <h2>Please Wait</h2> 
      }
    </div>
  </TitledLayout>;
}
