import { Suspense, lazy, useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import Background from './Background';

import './App.css';
import { setCurrentScreen } from '../reducers/actions/game';
import Bgm from './Bgm';
import Preloader from './screens/Preloader';

function App() {
  const Menu = lazy(() => import('./screens/Menu'));
  const Settings = lazy(() => import('./screens/Settings')); // todo: change
  const Play = lazy(() => import('./screens/Play'));
  const Credits = lazy(() => import('./screens/Credits'));

  const dispatch = useDispatch();
  const currentScreen = useSelector((state) => state.game.currentScreen);
  const scheduledScreen = useSelector((state) => state.game.scheduledScreen);
  
  useEffect(() => {
    if (scheduledScreen !== null) {
      const id = setTimeout(() => {
        dispatch(setCurrentScreen(scheduledScreen));
      }, 300);
      return () => clearTimeout(id);
    }
  }, [scheduledScreen, dispatch]);

  const renderedScreen = useMemo(() => {
    switch (currentScreen) {
      case 'preloader':
        return <Preloader />;
      case 'menu':
        return <Menu />;
      case 'settings':
        return <Settings />;
      case 'play':
        return <Play />;
      case 'credits':
        return <Credits />;
      default:
        return <div>Unknown screen! :c</div>;
    }
  }, [currentScreen]);

  const screenOpacity = scheduledScreen ? 0 : 1;

  return (
    <div className="App">
      <div className={`App-cursor-mask ${scheduledScreen ? 'active' : ''}`}></div>
      <Bgm />
      <Background />
      <main className="App-main" style={{opacity: screenOpacity}}>
        <Suspense fallback={<div>Loading...</div>}>
          {renderedScreen}
        </Suspense>
      </main>
    </div>
  );
}

export default App;
