import menuBackgroundSrc from '../assets/bg/menu_bg.jpg';
import playBackgroundSrc from '../assets/bg/play_bg.jpg';
import settingsBackgroundSrc from '../assets/bg/settings_bg.jpg';
import './Background.css';
import { useSelector } from 'react-redux';

function Background() {
  const currentScreen = useSelector((state) => state.game.currentScreen);
  const scheduledScreen = useSelector((state) => state.game.scheduledScreen);

  const activeScreen = scheduledScreen ?? currentScreen;
  const outgoingScreen = scheduledScreen && currentScreen;

  const screens = [
    ['menu', menuBackgroundSrc],
    ['play', playBackgroundSrc],
    ['settings', settingsBackgroundSrc],
    ['credits', settingsBackgroundSrc],
  ]

  return (
    <div className="bg-carousel">
      {screens.map(([screenName, backgroundSrc]) => 
        <div key={screenName}
          className={`bg-slide ${activeScreen === screenName ? 'active' : ''} ${outgoingScreen === screenName ? 'outgoing' : ''}`} 
          style={{backgroundImage: `url(${backgroundSrc})`}}
        ></div>
      )}
    </div>
  );
}

export default Background;
