import { SET_CURRENT_SCREEN, SCHEDULE_SCREEN_CHANGE, SET_SETTING } from './actions/game'

const initialState = {
  currentScreen: 'preloader',
  scheduledScreen: null,
  sfxVolume: 10,
  uiVolume: 10,
  bgmVolume: 7,
};

const gameReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_CURRENT_SCREEN:
      return { ...state, currentScreen: action.payload, scheduledScreen: null };
    case SCHEDULE_SCREEN_CHANGE:
      return { ...state, scheduledScreen: action.payload };
    case SET_SETTING:
      return { ...state, [action.payload.setting]: action.payload.value}
    default:
      return state;
  }
};

export default gameReducer;