
export const SET_ROUND_PHASE = 'SET_ROUND_PHASE';
export const SETUP_ROUND = 'SETUP_ROUND';
export const ADD_LOCAL_PLAYER_INPUT = 'ADD_LOCAL_PLAYER_INPUT';
export const UPDATE_PHASE_SECONDS_LEFT = 'UPDATE_PHASE_SECONDS_LEFT';

export const setupRound = ({ bans, ongoingPhaseDuration }) => ({
  type: SETUP_ROUND,
  payload: { bans, ongoingPhaseDuration }
});

export const setRoundPhase = (phase) => ({
  type: SET_ROUND_PHASE,
  payload: phase
});

export const addLocalPlayerInput = (symbol) => ({
  type: ADD_LOCAL_PLAYER_INPUT,
  payload: symbol
});

export const updatePhaseSecondsLeft = () => ({
  type: UPDATE_PHASE_SECONDS_LEFT
})
